@import 'ScssHelpers/helpers-all.scss';

.form {
	padding-top: 6.6rem;

	@include bp($point_2, min) {
		padding-bottom: 12.4rem;
	}
	@include bp($point_3) {
		padding-bottom: 6.4rem;
	}
}

.form__title {
	color: #bdbdbd;
	font-weight: 300;
	line-height: 1;

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}

	@include bp($point_2, min) {
		font-size: 6.4rem;
	}
	@include bp($point_3) {
		font-size: 3.4rem;
		text-align: center;
	}
}

.form__row {
	@include flex_block(row, wrap, flex-start);
}

.form__col {
	&.form__col--info {
		@include bp($point_2, min) {
			padding-right: 3rem;
			width: 36%;
		}
		@include bp($point_3) {
			width: 100%;
		}
	}

	&.form__col--img {
		@include bp($point_2, min) {
			width: 64%;
		}
		@include bp($point_3) {
			width: 100%;
			order: -1;

			margin-bottom: 3rem;
		}
	}
}

.form__item_row {
	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.form__btn {
	margin-top: 2rem;
}

.form__img_w {
	display: block;
	width: 100%;
	height: 100%;
}

.form__img {
	@include cover_img;
}
