@import 'ScssHelpers/helpers-all.scss';

.task {
	padding-top: 6.5rem;
}

.task__list {
	@include flex_block(row, wrap, flex-start, stretch, stretch);

	margin: 0 -3rem -3rem 0;

	counter-reset: item;
}

.task__item {
	// @include flex_block(row, nowrap, flex-start, stretch, stretch);
	position: relative;

	padding: 0 3rem 3rem 0;

	&:before {
		height: 100%;
		counter-increment: item;
		content: counter(item);

		font-family: $font_2;
		line-height: 80%;
		font-weight: 700;
		color: #bdbdbd;

		@include bp($point_2, min) {
			display: inline;
			font-size: 20rem;
		}
		@include bp($point_3) {
			font-size: 4.5rem;
		}
	}

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, stretch, stretch);
		width: 50%;
	}

	@include bp($point_3) {
		text-align: center;
		width: 100%;
	}
}

.task__descr {
	font-size: 2.2rem;
	line-height: 163%;
	color: $gray;

	width: 100%;

	@include bp($point_2, min) {
		padding-left: 3rem;
	}

	.task__item:nth-child(odd) & {
		@include bp($point_2, min) {
			max-width: 34rem;
		}
	}
}

.task__title {
	color: #bdbdbd;
	font-weight: 300;
	line-height: 1;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6.4rem;
		}
		@include bp($point_3) {
			margin-bottom: 3.4rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 6.4rem;
	}
	@include bp($point_3) {
		text-align: center;
		font-size: 3.4rem;
	}
}
