@import 'ScssHelpers/helpers-all.scss';

.company {
	padding-top: 8.5rem;
}

.company__row {
	position: relative;

	padding: 0 2rem;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch, stretch);
	}

	&:after {
		position: absolute;
		top: -1.6rem;
		left: 0;
		z-index: -1;

		display: block;
		width: 100%;
		height: 97%;
		content: '';
		background-color: #fbfbfb;

		pointer-events: none;
	}
}

.company__col {
	&.company__col--img_mod {
		@include bp($point_2, min) {
			width: 60%;
		}
		@include bp($point_3) {
			width: 100%;

			&:not(:last-child) {
				margin-bottom: 2rem;
			}
		}
	}

	&.company__col--info_mod {
		@include bp($point_2, min) {
			width: 40%;
		}
		@include bp($point_3) {
			width: 100%;
		}
	}
}

.company__col_in {
	margin-left: auto;

	width: 100%;

	position: relative;

	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: repeat(8, 3rem);

	gap: 3rem;

	@include bp($point_2, min) {
		max-width: 60rem;
		padding-right: 3rem;
	}
}

.company__img_w {
	display: block;

	width: 100%;
	height: 100%;

	&.company__img_w--1_mod {
		grid-row: 1 / 6;
	}

	&.company__img_w--2_mod {
		grid-row: 2 / 8;
	}

	&.company__img_w--3_mod {
		grid-row: 6 / 9;
	}
}

.company__img {
	@include cover_img;
}

.company__article {
	display: block;

	width: 100%;

	@include bp($point_2, min) {
		max-width: 38rem;
	}

	&:not(:last-child) {
		margin-bottom: 2.2rem;
	}

	h2 {
		color: #bdbdbd;
		font-weight: 300;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}

		@include bp($point_2, min) {
			font-size: 6.4rem;
		}
		@include bp($point_3) {
			font-size: 3.4rem;
		}
	}

	p {
		line-height: 156%;
		font-weight: 300;
		font-size: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}
