@import 'ScssHelpers/helpers-all.scss';

.project {
	padding-top: 3.2rem;
}

.project__title {
	font-size: 6.4rem;
	color: #bdbdbd;
	font-weight: 300;
	line-height: 1;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6.4rem;
		}
		@include bp($point_3) {
			margin-bottom: 3.4rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 6.4rem;
	}
	@include bp($point_3) {
		font-size: 3.4rem;
	}
}

.project__list {
	@include flex_block(row, wrap, flex-start, stretch, stretch);

	@include bp($point_2, min) {
		margin: 0 -3rem -3rem 0;
	}
	@include bp($point_3) {
		margin: 0 -2rem -2rem 0;
	}
}

.project__list_w {
	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.project__item {
	min-height: 28.5rem;

	cursor: pointer;

	@include bp($point_2, min) {
		padding: 0 3rem 3rem 0;

		&:nth-child(5n + 1) {
			width: 50%;
		}
		&:nth-child(6n + 2) {
			width: 50%;
		}
		&:nth-child(7n + 3) {
			width: 25%;
		}
		&:nth-child(8n + 4) {
			width: 42%;
		}
		&:nth-child(9n + 5) {
			width: 33%;
		}
	}
	@include bp($point_3) {
		width: 50%;
		padding: 0 2rem 2rem 0;

		&:nth-child(odd):last-child {
			width: 100%;
		}
	}
}

.project__item_in {
	position: relative;

	height: 100%;

	&:after {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		background-color: #333333;

		content: '';

		opacity: 0;
		transition: opacity ease 0.4s;
	}

	&.project__item--active_state:after,
	&:hover:after {
		opacity: 0.8;
	}
}

.project__img_w {
	position: absolute;
	top: 0;
	left: 0;

	display: block;

	width: 100%;
	height: 100%;
}

.project__img {
	@include cover_img;
}

.project__item_link {
	color: $white;
}

.project__item_link_icon {
	display: inline-block;

	margin-left: 1rem;
	width: 1.8rem;
	height: 0.8rem;
}

.project__btn_w {
	@include flex_block(row, wrap, flex-end, center, center);
}

.project__item_info {
	@include flex_block(row, wrap, center, center, center);

	position: relative;
	z-index: 1;

	height: 100%;
	padding: 1rem;

	opacity: 0;
	transition: opacity ease 0.4s;

	.project__item--active_state &,
	.project__item_in:hover & {
		opacity: 1;
	}
}

.project__item_title {
	max-width: 41.5rem;
	width: 100%;

	color: $white;
	line-height: 1;
	font-weight: 700;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	@include bp($point_2, min) {
		font-size: 6.4rem;
	}
	@include bp($point_3) {
		font-size: 2.6rem;
	}
}

.project__item_link {
	max-width: 41.5rem;
	width: 100%;

	font-size: 1.2rem;
	letter-spacing: 0.2em;

	&:focus,
	&:hover {
		text-decoration: none;
	}
}
